import { Send } from "@mui/icons-material";
import { Box, Card, IconButton, InputAdornment, InputBase, styled } from "@mui/material"
import { useEffect, useState } from "react";
import { useMiddleware } from "../../../../Application";
import TextField from "../../../../components/TextField";


const StyledSearchIcon = styled(Send)(({ theme }) => ({
    color: theme.palette.text.secondary
}))

const StyledInputBase = styled(InputBase)(({ theme }) => ({
    fontWeight: theme.typography.fontWeightMedium,
    fontSize: theme.typography.button.fontSize
}))

const MessageForm = ({ ticket, onMessageSent }) => {

    const [message, setMessage] = useState('');

    const middleware = useMiddleware('tickets.main');

    const sendMessage = async (e) => {
        e.preventDefault();

        const response = await middleware.createOrUpdate({
            ...ticket,
            messages: [
                ...ticket.messages,
                {
                    content: message,
                    sentAt: new Date(),
                    isRespond: false
                }
            ]
        });
        if (response.ok) {
            setMessage('');
            onMessageSent(response.ticket);
        }
    }


    return (
        <Card variant="outlined" sx={{ px: 2, py: 1, display: 'flex', mb: 3 }}>
            <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'flex-start', flexGrow: 1 }}>
                {/* <StyledSearchIcon sx={{ mr: 1 }} color="inherit" /> */}
                <StyledInputBase
                    placeholder="پیام"
                    value={message}
                    onChange={e => setMessage(e.target.value)}
                    sx={{ flexGrow: 1 }}
                    multiline
                />
            </Box>
            <Box>
                <IconButton
                    onClick={sendMessage}
                    sx={{ transform: 'rotate(180deg)' }}
                >
                    <Send />
                </IconButton>
            </Box>
        </Card>
    )
}

export default MessageForm;