import { Avatar, Box, Card, Typography } from "@mui/material";
import format from "date-fns-jalali/format";
import orderBy from "lodash/orderBy";
import { useState } from "react";
import { useLocation, useNavigate, useSearchParams } from "react-router-dom";
import useAfterFirstRender from "../../../../utils/useAfterFirstRender";


const MessagesList = ({
    ticket
}) => {

    const navigate = useNavigate();
    const location = useLocation();

    const [searchParams, setSearchParams] = useSearchParams();

    const [sort, setSort] = useState(
        searchParams.has('sort') ? searchParams.get('sort') : '-createdAt'
    );


    useAfterFirstRender(() => {
        setSearchParams({
            ...Object.fromEntries(searchParams),
            sort
        })
    }, [sort]);


    return (
        <>
            {orderBy(ticket.messages, 'sentAt', 'desc').map((message, index) => (
                <Box key={index} sx={{ mb: 2 }}>
                    <Box sx={{
                        display: 'flex',
                        flexDirection: message.isRespond ? 'row-reverse' : 'row',
                        justifyContent: 'flex-start'
                    }}>
                        <Avatar
                            src="asd"
                            sx={{
                                mr: !message.isRespond && 2,
                                ml: message.isRespond && 2
                            }}
                        />
                        <Card
                            variant="outlined"
                            sx={{
                                flexBasis: { xs: '100%', md: '33.33%' },
                                maxWidth: { xs: '100%', md: '50%' }
                            }}
                        >
                            <Typography sx={{ mb: 2, p: 2 }}>{message.content}</Typography>
                            <Box sx={{ px: 2, py: 1 }}>
                                <small>{format(new Date(message.sentAt), 'yyyy/MM/dd hh:mm')}</small>
                            </Box>
                        </Card>
                    </Box>
                </Box>
            ))}
        </>
    )
}

export default MessagesList;