import HeadsetMic from "@mui/icons-material/HeadsetMicOutlined";
import { Box, Breadcrumbs, Grid, Typography } from "@mui/material";
import { useState } from "react";
import { Navigate, useLocation } from "react-router-dom";
import { TabView } from "../../../../tabs";
import MessageForm from "./MessageForm";
import MessageList from "./MessageList";


const Main = () => {

    const location = useLocation();

    // const ticket = location.state?.ticket;

    const [ticket, setTicket] = useState(location.state?.ticket);

    if (!ticket) {
        return (
            <Navigate to="/support/tickets" replace />
        )
    }

    return (
        <TabView title={`تیکت پشتیبانی`} Icon={HeadsetMic}>
            <Box sx={{ p: { xs: 1, sm: 2, md: 3 } }}>
                <Grid container justifyContent="center">
                    <Grid item xs={12} md={8}>
                        <Box
                            sx={{
                                mb: 1,
                                display: "flex",
                                justifyContent: "space-between",
                                alignItems: "center"
                            }}
                        >
                            <Box>
                                <Typography variant="h5" sx={{ fontWeight: 600, mb: 1 }}>
                                    {ticket.subject}
                                </Typography>
                                <Breadcrumbs>
                                    <Typography color="text.primary">پشتیبانی</Typography>
                                    <Typography color="text.primary">تیکت‌ها</Typography>
                                    <Typography color="text.primary">{ticket.subject}</Typography>
                                </Breadcrumbs>
                            </Box>
                        </Box>
                        <MessageForm ticket={ticket} onMessageSent={t => setTicket(t)} />
                        <MessageList ticket={ticket} />
                    </Grid>
                </Grid>
            </Box>
        </TabView>
    )
}

export default Main;